/* Original source code modified from:
https://github.com/tiagobalmeida/purecss-circular-progress-bar */

.circle-prog-widget {
    width: fit-content;
    padding: 5px;
    background-color: #000;
    border-radius: 50%;
 }
 
 #circle-container {
    padding: 3px;
    width: fit-content;
    border-radius: 50%;
 }
 
 .outer-color-black {
    background-color: #000;
 }
 .outer-color-green {
    background-color:#23b100;
 }
 .outer-color-yellow {
    background-color:yellow;
  }
  .outer-color-orange {
    background-color:#ffbc40;;
  }
  .outer-color-red {
    background-color:#ff3333;
  }
 
 .progress-circle {
     font-size: 40px;
     margin: 20px;
     position: relative;
     padding: 0;
     width: 5em;
     height: 5em;
     background-color: #fff; 
     border-radius: 50%;
     line-height: 5em;
  }
  
  .progress-circle:after{
      border: none;
      position: absolute;
      top: 0.35em;
      left: 0.35em;
      text-align: center;
      display: block;
      border-radius: 50%;
      width: 4.3em;
      height: 4.3em;
      background-color: white;
      content: " ";
  }
 
  .progress-circle span {
      position: absolute;
      top: 15%;
      left: 15%;
      border-radius: 50%;
      line-height: 3.5em;
      width: 5em;
      text-align: center;
      display: block;
      z-index: 2;
      background-color: #000;
  }
 
  #number-display {
    width: 3.5em;
    height: 3.5em;
  }
 
  .span-green {
    color:#23b100;
  }
  .span-yellow {
    color:yellow;
  }
  .span-orange {
    color:#ffbc40;
  }
  .span-red {
    color:#ff3333;
  }
 
 
  .left-half-clipper { 
     border-radius: 50%;
     width: 5em;
     height: 5em;
     position: absolute;
     clip: rect(0, 5em, 5em, 2.5em);
  }
 
  .progress-circle.over50 .left-half-clipper {
     clip: rect(auto,auto,auto,auto);
  }
 
 /* -------------------- Progress up to 50 */
 
  .value-bar {
     position: absolute;
     clip: rect(0, 2.5em, 5em, 0);
     width: 5em;
     height: 5em;
     border-radius: 50%;
     box-sizing: border-box;  
     border: 0.45em solid #000; 
  }
 
 .value-bar-green {
    border: 0.45em solid #156800; 
 }
 .value-bar-yellow {
    border: 0.45em solid #d9ff04; 
 }
 .value-bar-orange {
    border: 0.45em solid #ffa600; 
 }
 .value-bar-red {
    border: 0.45em solid #ff0000; 
 }
 
  /* ------------------- Progress past 50*/
 
  .progress-circle.over50 .first50-bar {
     position: absolute;
     clip: rect(0, 5em, 5em, 2.5em);
     border-radius: 50%;
     width: 5em;
     height: 5em;
     background-color: #000;
  }
 
 .progress-circle.over50-green {
    background-color: #156800;
 }
 .progress-circle.over50-yellow {
    background-color: #d9ff04;
 }
 .progress-circle.over50-orange {
    background-color: #ffa600;
 }
 .progress-circle.over50-red {
    background-color: #ff0000;
 }
 
 
  .progress-circle:not(.over50) .first50-bar{ display: none; }
  
